  }
  .slider__btn--before:hover,
  .slider__btn--after:hover {
    border-bottom: 1px solid #d8d8d8;
  }
}
@media (min-width: 1200px) {
  .slider__before {
    min-width: 325px;
    min-height: 517px;
  }
  .slider__before--img {
    width: 690px;
    margin-left: -40px;
  }
  .slider__after {
    min-width: 373px;
    min-height: 517px;
  }
  .slider__after--img {
    margin-left: -97%;
  }
  .slider__switcher {
    justify-content: flex-start;
    min-height: 146px;
